import {createContentCard} from "./content";
import {contentSection} from "./content";

const dropDownMenu = document.querySelector(".list");

// –û—Ç—Ä–∏—Å–æ–≤–∫–∞ –∫–∞—Ä—Ç–∏–Ω–æ–∫-–ø–∏–Ω–æ–≤ –≤ –∑–∞–≤–∏—Å–∏–º–æ—Å—Ç–∏ –æ—Ç –∫–ª–∏–∫–∞ –Ω–∞ –æ–¥–Ω—É –∏–∑ –¥–æ—Å–æ–∫ (—Ä–∞–∑–ª–∏—á–∞—é—Ç—Å—è –ø–æ ID).
// –ü—Ä–æ–∏—Å—Ö–æ–¥–∏—Ç –æ—á–∏—Å—Ç–∫–∞ –≤—Å–µ–≥–æ –∫–æ–Ω—Ç–µ–Ω—Ç–∞. –í —Å–ª—É—á–∞–µ –µ—Å–ª–∏ –¥–æ—Å–∫–∞ –ø—É—Å—Ç–∞ –ø—Ä–æ–∏—Å—Ö–æ–¥–∏—Ç –¥–æ–±–∞–≤–ª–µ–Ω–∏–µ
// —Ç–µ–∫—Å—Ç–∞ "–ù–∞ –¥–æ—Å–∫—É –µ—â—ë –Ω–µ –¥–æ–±–∞–≤–ª–µ–Ω—ã –ø–∏–Ω—ã".
dropDownMenu.addEventListener("click", (event) => {
    // console.log(event.target)
    switch (event.target.id) {
        case "listBtn1":
            contentSection.innerHTML = ""
            const board1 = JSON.parse(localStorage.getItem("board1"))
            board1 === null ? contentSection.innerText = "–ù–∞ –¥–æ—Å–∫—É –µ—â—ë –Ω–µ –¥–æ–±–∞–≤–ª–µ–Ω—ã –ø–∏–Ω—ã"
                : board1.map(item => createContentCard(item))
            break
        case "listBtn2":
            contentSection.innerHTML = ""
            const board2 = JSON.parse(localStorage.getItem("board2"))
            board2 === null ? contentSection.innerText = "–ù–∞ –¥–æ—Å–∫—É –µ—â—ë –Ω–µ –¥–æ–±–∞–≤–ª–µ–Ω—ã –ø–∏–Ω—ã"
                : board2.map(item => createContentCard(item))
            break
        case "listBtn3":
            contentSection.innerHTML = ""
            const board3 = JSON.parse(localStorage.getItem("board3"))
            board3 === null ? contentSection.innerText = "–ù–∞ –¥–æ—Å–∫—É –µ—â—ë –Ω–µ –¥–æ–±–∞–≤–ª–µ–Ω—ã –ø–∏–Ω—ã"
                : board3.map(item => createContentCard(item))
            break
        default:
    }
})