document.querySelector("#main-search").oninput = function () {
    let val = this.value.trim();
    let elasticItems = document.querySelectorAll(".content-cart")
    if (val !== '') {
        elasticItems.forEach(function (elem) {
            if (elem.innerText.search(RegExp(val,"gi")) === -1) {  // —Ñ–ª–∞–≥–∏: i - –° —ç—Ç–∏–º —Ñ–ª–∞–≥–æ–º –ø–æ–∏—Å–∫ –Ω–µ –∑–∞–≤–∏—Å–∏—Ç –æ—Ç —Ä–µ–≥–∏—Å—Ç—Ä–∞: –Ω–µ—Ç —Ä–∞–∑–Ω–∏—Ü—ã –º–µ–∂–¥—É A –∏ a.
                elem.classList.add("hide");                       //  g - –° —ç—Ç–∏–º —Ñ–ª–∞–≥–æ–º –ø–æ–∏—Å–∫ –∏—â–µ—Ç –≤—Å–µ —Å–æ–≤–ø–∞–¥–µ–Ω–∏—è, –±–µ–∑ –Ω–µ–≥–æ ‚Äì —Ç–æ–ª—å–∫–æ –ø–µ—Ä–≤–æ–µ.
            }
            else{
                elem.classList.remove("hide");
            }
        });
    }
    else {
        elasticItems.forEach(function (elem) {
            elem.classList.remove("hide");
        });
    }
}
