import {createContentCard} from "./content";
import {contentSection} from "./content";

const headerLogo = document.querySelector(".header-logo");

// –ü—Ä–∏ –∫–ª–∏–∫–µ –Ω–∞ –ª–æ–≥–æ –ø–æ–ª—É—á–∞–µ–º –º–∞—Å—Å–∏–≤ —Å –∫–∞—Ä—Ç–æ—á–∫–∞–º–∏-–ø–∏–Ω–∞–º–∏, –æ—á–∏—â–∞–µ–º –∫–æ–Ω—Ç–µ–Ω—Ç –∏ –∑–∞–Ω–æ–≤–æ –æ—Ç—Ä–∏—Å–æ–≤—ã–≤–∞–µ–º –≤—Å–µ –∫–∞—Ä—Ç–æ—á–∫–∏,
// –¥–ª—è —Ç–æ–≥–æ, —á—Ç–æ–±—ã –≤–µ—Ä–Ω—É—Ç—å—Å—è –∏–∑ –¥–æ—Å–∫–∏ –∫ –∏–∑–Ω–∞—á–∞–ª—å–Ω–æ–º—É –∫–æ–Ω—Ç–µ–Ω—Ç—É
headerLogo.addEventListener("click", (event) => {
    const images = JSON.parse(localStorage.getItem("images"))
    contentSection.innerHTML = ""
    images.map(item => createContentCard(item))
})
